import jagerOriginal from './media/products/sss2.png';
import jagerManifest from './media/products/prod_ssec_img.png';
import jagerColdBrew from './media/products/jager_cold_brew.png';

import { SET_PATH_URL   }  from "./constants/action-types";
import { SET_CART_PRODUCTS   }  from "./constants/action-types";


    const initialState = {
         lastPathUrl         : '',
         propsCartProducts: []
  }



    function rootReducer(state = initialState, action) {

    
      switch(action.type) {
        case SET_PATH_URL: 
           return Object.assign({}, state, {
            lastPathUrl: action.payload.lastPathUrl
        });
        break;
        case SET_CART_PRODUCTS: 
            return Object.assign({}, state, {
              propsCartProducts: action.payload.propsCartProducts
          });
          break;

      }

      return state;
    }



  export default rootReducer;